@import '../../styles/constants';

.ABBYY_Capture_Module__MessagePage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  background-color: $backgroundColor;

  .ABBYY_Capture_Module__MessagePage-Icon {
    width: 48px;
    height: 48px;
    align-self: center;
    margin: 8px;
    object-fit: contain;
    background: url(./warning.svg);
  }
  .ABBYY_Capture_Module__MessagePage-Text {
    color: #000000;
    text-align: center;
    font-weight: normal;
  }

  svg {
    .ABBYY_Capture_Module__MessagePage-loader {
      animation-name: spin;
      animation-duration: 1.2s;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
      transform-origin: 50% 50% 0;
    }

    @keyframes spin {
      100% {
        transform: rotate(360deg);
      }
    }
  }
}
