@import '../../../../styles/constants';

$animationSize: 34px;
$arrowSize: 44px;
$animationWidth: 10px;
$animationPadding: 4px;
$top: -10px;
$indent: 16px;

@keyframes arrowAnimation {
  0% {
    background-position-x: $animationPadding;
  }
  50% {
    background-position-x: $animationWidth + $animationPadding;
  }
  100% {
    background-position-x: $animationPadding;
  }
}

.ABBYY_Capture_Module {
  .ABBYY_Capture_Module__Container {
    .ABBYY_Capture_Module_CaptureView {
      .online_capture-document_title-closer {
        border-radius: 8px;
        background-color: rgba(0, 0, 0, 0.5);
        color: #ffdc48;
        text-align: center;

        .online_capture-document_title-closer_dots {
          text-align: center;

          @keyframes scale {
            0% {
              transform: scale(0);
            }
            11% {
              transform: scale(0.5);
              display: inline-block;
            }
            22% {
              transform: scale(0.75);
            }
            33% {
              transform: scale(1);
            }
            44% {
              transform: scale(0.75);
            }
            55% {
              transform: scale(0.5);
            }
            66% {
              transform: scale(0);
            }
            100% {
              transform: scale(0);
            }
          }

          span {
            width: 8px;
            height: 8px;
            margin-right: 5px;
            display: inline-block;
            animation: scale 0.9s infinite;
            -webkit-animation: scale 1.2s infinite;
            border-radius: 50%;
            background-color: $backgroundColor;

            &:nth-child(2) {
              animation-delay: 0.1s;
            }

            &:nth-child(3) {
              animation-delay: 0.2s;
            }
          }
        }
      }

      .online_capture-document_closer-animation {
        position: absolute;
      }
    }
  }

  .ABBYY_Capture_Module__Container.mobile {
    .ABBYY_Capture_Module_CaptureView {
      .online_capture-document_closer-animation {
        &:after,
        &:before {
          display: inline-block;
          content: '';
          width: $arrowSize + $animationWidth + $animationPadding;
          height: $arrowSize;
          position: relative;
          background-image: url('./assets/arrow.svg');
          background-repeat: no-repeat;
          animation: arrowAnimation 1.2s infinite;
        }

        svg.online_capture-closer {
          position: absolute;
          display: block;
        }

        .online_capture-document_closer-animation_landscape_top,
        .online_capture-document_closer-animation_landscape_bottom {
          display: none;
        }
      }
    }
  }

  .ABBYY_Capture_Module__Container.mobile.landscape {
    .ABBYY_Capture_Module_CaptureView {
      .online_capture-document_closer-animation {
        width: 44px;
        height: 100%;

        &:before {
          top: 24px;
          left: -7px;
          transform: rotate(90deg);
        }

        &:after {
          top: calc(100% - 117px);
          left: -7px;
          transform: rotate(-90deg);
        }

        svg.online_capture-closer {
          &.online_capture-closer.rec-0 {
            top: -$indent;
            left: 19px;
            transform: rotate(90deg);
          }

          &.online_capture-closer.rec-180 {
            right: 19px;
            transform: rotate(-90deg);
            bottom: -$indent;
          }
        }
      }
    }
  }

  .ABBYY_Capture_Module__Container.mobile.portrait {
    .ABBYY_Capture_Module_CaptureView {
      .online_capture-document_closer-animation {
        width: 100%;
        height: 44px;

        &:before {
          left: 20px;
          transform: none;
        }

        &:after {
          left: calc(100% - 136px);
          transform: rotate(180deg);
        }

        svg.online_capture-closer {
          &.online_capture-closer.rec-0 {
            top: $top;
            left: $indent;
          }

          &.online_capture-closer.rec-180 {
            right: $indent;
            transform: rotate(180deg);
            top: $top;
          }
        }
      }
    }
  }

  .ABBYY_Capture_Module__Container.tablet,
  .ABBYY_Capture_Module__Container.desktop {
    .ABBYY_Capture_Module_CaptureView {
      .online_capture-document_closer-animation {
        height: 280px;
        width: 280px;

        svg.online_capture-closer {
          display: none;
        }

        .online_capture-document_closer-animation_landscape_top,
        .online_capture-document_closer-animation_landscape_bottom {
          position: absolute;
          width: 100%;

          &:after,
          &:before {
            display: inline-block;
            content: '';
            width: $arrowSize + $animationWidth + $animationPadding;
            height: $arrowSize;
            position: relative;
            background-image: url('./assets/arrow.svg');
            background-repeat: no-repeat;
            animation: arrowAnimation 1.2s infinite;
          }

          &:before {
            left: -6px;
          }

          &:after {
            left: calc(100% - 110px);
          }
        }

        .online_capture-document_closer-animation_landscape_top {
          &:before {
            transform: rotate(45deg);
          }

          &:after {
            transform: rotate(135deg);
          }
        }

        .online_capture-document_closer-animation_landscape_bottom {
          bottom: 0;

          &:before {
            transform: rotate(-45deg);
          }

          &:after {
            transform: rotate(-135deg);
          }
        }
      }
    }
  }

  .ABBYY_Capture_Module__Container.mobile {
    .ABBYY_Capture_Module_CaptureView {
      .online_capture-document_title-closer {
        padding: 5px 14px;
        font-weight: 100;
        width: 140px;
        height: 52px;
      }
    }
  }

  .ABBYY_Capture_Module__Container.tablet,
  .ABBYY_Capture_Module__Container.desktop {
    .ABBYY_Capture_Module_CaptureView {
      .online_capture-document_title-closer {
        padding: 5px 14px;
        font-weight: normal;
        width: 100px;
        height: 52px;
      }
    }
  }
}
