.ABBYY_Capture_Module {
  .ABBYY_Capture_Module__TopPanel {
    .ABBYY_Capture_Module__ToolBar_button {
      font-size: 14px;
      padding: 0 8px;
      overflow: hidden;
      text-overflow: ellipsis;
      width: 25%;
      cursor: pointer;
      color: #4a8ee0;
      text-align: center;
      position: relative;

      p {
        margin: 0;
        -webkit-font-smoothing: antialiased;
      }

      &.ABBYY_Capture_Module__ToolBar_button--disabled {
        opacity: 0.3;
      }
    }
  }
}
